import React, { Fragment, useState, useEffect } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Container from '@material-ui/core/Container'

import clsx from 'clsx'

import { Button } from '@material-ui/core'
import { DropzoneArea } from 'material-ui-dropzone'

import PropTypes from "prop-types"
import TextField from "@material-ui/core/TextField"
import MenuItem from "@material-ui/core/MenuItem"
import { createCustomer, updateCustomer } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';



const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any.isRequired
    })
  ])
}
const CustomerForm = props => {


  useEffect(() => {
    console.log(props)

    if (props.customer) {
      setName(props.customer.name);
      setNit(props.customer.nit);
      setAddress(props.customer.address);
      setPhone(props.customer.phone);
      setEmail(props.customer.email);
    }
  }, [])

  const [disabled, setDisabled] = useState(false)

  const classes = useStyles()


  const [name, setName] = useState('');
  const [nit, setNit] = useState('');
  const [address, setAddress] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');

  const [errorName, setErrorName] = useState(false)
  const [errorTextName, setErrorTextName] = useState('')

  const [errorNit, setErrorNit] = useState(false)
  const [errorTextNit, setErrorTextNit] = useState('')

  const [errorAddress, setErrorAddress] = useState(false)
  const [errorTextAddress, setErrorTextAddress] = useState('')

  const [errorPhone, setErrorPhone] = useState(false)
  const [errorTextPhone, setErrorTextPhone] = useState('')

  const [errorEmail, setErrorEmail] = useState(false)
  const [errorTextEmail, setErrorTextEmail] = useState('')

  const onChangeName = e => {
    setName(e.target.value);
    validateName(e.target.value);
  }
  const onChangeNit = e => {
    setNit(e.target.value);
    validateNit(e.target.value);
  }
  const onChangeAddress = e => {
    setAddress(e.target.value)
  }
  const onChangePhone = e => {
    setPhone(e.target.value)
  }
  const onChangeEmail = e => {
    setEmail(e.target.value)
  }

  const validateForm = () => {
    let errors = 0
    errors += validateName()
    errors += validateEmail()
    errors += validateNit()
    return errors
  }

  const validateName = (value) => {
    let text = value !== undefined ? value : name

    if (text == null || text.toString().trim() === '') {
      setErrorName(true)
      setErrorTextName('Este campo es requerido')
      return 1
    }
    else {
      setErrorName(false)
      setErrorTextName('')
      return 0
    }
  }

  const validateNit = (value) => {
    let text = value !== undefined ? value : nit

    if (text == null || text.toString().trim() === '') {
      setErrorNit(true)
      setErrorTextNit('Este campo es requerido')
      return 1
    }
    else {
      setErrorNit(false)
      setErrorTextNit('')
      return 0
    }
  }

  const validateEmail = (value) => {
    let text = value !== undefined ? value : email

    if (text == null || text.toString().trim() === '') {
      setErrorEmail(true)
      setErrorTextEmail('Este campo es requerido')
      return 1
    }
    else {
      setErrorEmail(false)
      setErrorTextEmail('')
      return 0
    }
  }



  const onSubmitCustomer = _ => {

    let errores = validateForm()


    if (errores === 0) {


      let data = {
        name,
        nit,
        address,
        phone,
        email
      }

      if (!props.customer) {
        createCustomer(data).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info('Cliente registrado exitosamente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
            props.onCloseModal(true);
            props.onUpdateCustomers();
          } else {
            toast.error(response.message, {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });

      } else {
        data.id = props.customer._id;
        updateCustomer(data).then(response => {
          console.log('response', response);
          if (response.status == 'success') {
            toast.info('Cliente actualizado exitosamente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
            props.onCloseModal(true);
            props.onUpdateCustomers();
          } else {
            toast.error('Ocurrió un error al registrar el cliente', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          }

        });
      }
    }
  }


  return (
    <Fragment>
      <ToastContainer />
      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmitCustomer()
            }} >
            <Grid container spacing={3}>


              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  error={errorName}
                  helperText={errorTextName}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre"
                  value={name}
                  onChange={onChangeName} />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  error={errorNit}
                  helperText={errorTextNit}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  value={nit}
                  onChange={onChangeNit}
                  label="Nit" />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Direccion"
                  value={address}
                  onChange={onChangeAddress} />
              </Grid>


              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Telefono"
                  onChange={onChangePhone} />
              </Grid>

              <Grid item xs={12} sm={4}>
                <TextField
                  fullWidth
                  error={errorEmail}
                  helperText={errorTextEmail}
                  margin="dense"
                  autoComplete='off'
                  id="outlined-simple-start-adornment"
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Correo"
                  onChange={onChangeEmail} />
              </Grid>





              <Grid item xs={2} sm={3}>
                <Button disabled={disabled} type="submit" variant="contained" color='primary'>
                  {props.customer ? "Actualizar" : "Guardar"}
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default CustomerForm