import React, { useState, useEffect, Fragment } from 'react'

import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

import CustomerDetail from './CustomerDetail'
import { Button } from '@material-ui/core';

import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import SearchIcon from '@material-ui/icons/Search';
import Slide from '@material-ui/core/Slide';
import RestoreIcon from '@material-ui/icons/Restore';
import CustomerForm from "../Customer/CustomerForm";

import Tooltip from '@material-ui/core/Tooltip'
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';
import TablePagination from '@material-ui/core/TablePagination';

import { withStyles } from '@material-ui/core/styles';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { deleteCustomer, restoreCustomer, listCustomers } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})



const Customers = _ => {

    useEffect(() => {
        listCustomers().then(response => {
            console.log(response);
            setCustomers(response.customers);
            setTotalCustomers(response.count);
        });
    }, []);

    const classes = useStyles()

    const [customers, setCustomers] = useState([])
    const [totalCustomers, setTotalCustomers] = useState(0)
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);


    const [open, setOpen] = useState(false);
    const [openRestoreCustomer, setOpenRestoreCustomer] = useState(false);
    const [openDetailCustomer, setOpenDetailCustomer] = useState(false);
    const [openEditCustomer, setOpenEditCustomer] = useState(false);
    const [openCreateCustomer, setOpenCreateCustomer] = useState(false);
    const [customer, setCustomerSelected] = useState({})


    const handleChangePage = (event, newPage) => {
        setPage(newPage);
        // getPersons({ variables: { start: newPage, end: rowsPerPage } })
    }

    const onUpdateCustomers = () => {
        listCustomers().then(response => {
            console.log(response);
            setCustomers(response.customers);
            setTotalCustomers(response.count);
        });
    }

    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        // getPersons({ variables: { start: 0, end: event.target.value } })
    }

    const onOpenDeleteCustomer = id => () => {
        setCustomerSelected(id)
        setOpen(true);
    }

    const onOpenRestoreCustomer = id => () => {
        setCustomerSelected(id)
        setOpenRestoreCustomer(true);
    }

    const onCloseDeleteCustomer = () => {
        setOpen(false);
    }

    const onDeleteCustomer = () => {
        setOpen(false);

        deleteCustomer(customer).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newCustomers = customers.map((currentCustomer) => {
                    if (customer !== currentCustomer._id)
                        return currentCustomer

                    currentCustomer.status = "inactivo";
                    return currentCustomer
                })
                setCustomers(newCustomers)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {

            toast.error('Ocurrió un error al desactivar el cliente', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })

    }

    const onRestoreCustomer = () => {

        setOpenRestoreCustomer(false)

        restoreCustomer(customer).then(response => {
            if (response.status === 'success') {
                toast.success(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                const newCustomers = customers.map((currentCustomer) => {
                    if (customer !== currentCustomer._id)
                        return currentCustomer

                    currentCustomer.estado = "activo";
                    return currentCustomer
                })
                setCustomers(newCustomers)
            } else {
                toast.error(response.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
            }
        }).catch(e => {
            toast.error('Ocurrió un error al activar el cliente', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        })
    }

    const onDetailCustomer = customer => () => {
        setCustomerSelected(customer)
        setOpenDetailCustomer(true)
    }

    const closeDetailCustomer = () => {
        setOpenDetailCustomer(false)
    }

    const onEditCustomer = customer => () => {
        setCustomerSelected(customer)
        setOpenEditCustomer(true)
    };
    const onCloseEditCustomer = () => {
        setOpenEditCustomer(false)
    };
    const onCloseRestoreCustomer = () => {
        setOpenRestoreCustomer(false)
    };

    const onCreateCustomer = () => {
        setOpenCreateCustomer(true)
    }
    const onCloseCreateCustomer = () => {
        setOpenCreateCustomer(false)
    }
    const onCloseModal = () => {
        setOpenCreateCustomer(false);
        setOpenEditCustomer(false);
    }


    return (
        <Fragment>
            <ToastContainer />
            <Paper className={classes.paper}>
                <Fab onClick={onCreateCustomer} variant="extended" color="primary" size="small" className={classes.margin}>
                    <AddIcon className={classes.extendedIcon} />
                    Nuevo cliente
                </Fab>

                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>NOMBRE</TableCell>
                            <TableCell>NIT</TableCell>
                            <TableCell>DIRECCION</TableCell>
                            <TableCell>TELEFONO</TableCell>
                            <TableCell>CORREO</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            customers.map(item => {

                                return (
                                    <TableRow key={item._id}>
                                        <TableCell>{item.name} </TableCell>
                                        <TableCell>{item.nit}</TableCell>
                                        <TableCell>{item.address}</TableCell>
                                        <TableCell>{item.phone}</TableCell>
                                        <TableCell>{item.email}</TableCell>


                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Detalle" placement="top">
                                                <SearchIcon className={classes.icon} onClick={onDetailCustomer(item)} />
                                            </Tooltip>
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title="Editar" placement="top">
                                                <EditIcon className={classes.icon} onClick={onEditCustomer(item)} />
                                            </Tooltip>
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            <Tooltip title={'Eliminar'} placement="top">
                                                {
                                                    item.status === 'activo' ?
                                                        <DeleteIcon className={classes.icon} onClick={onOpenDeleteCustomer(item._id)} />
                                                        :
                                                        <RestoreIcon className={classes.icon} onClick={onOpenRestoreCustomer(item._id)} />
                                                }
                                            </Tooltip>
                                        </TableCell>
                                    </TableRow>)
                            }
                            )}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalCustomers}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>


            <Dialog
                open={open}
                onClose={onCloseDeleteCustomer}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de desabilitar este cliente?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDeleteCustomer} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDeleteCustomer} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openRestoreCustomer}
                onClose={onCloseRestoreCustomer}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de habilitar este cliente?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseRestoreCustomer} color="primary">
                        NO
                    </Button>
                    <Button onClick={onRestoreCustomer} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openDetailCustomer} onClose={closeDetailCustomer} TransitionComponent={Transition}>
                <DialogContent>
                    <CustomerDetail customer={customer} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={closeDetailCustomer} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openEditCustomer} onClose={onCloseEditCustomer} TransitionComponent={Transition}>
                <DialogContent>
                    <CustomerForm onCloseModal={onCloseModal} onUpdateCustomers={onUpdateCustomers} customer={customer} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseEditCustomer} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                fullWidth={true}
                maxWidth="md"
                open={openCreateCustomer}>
                <DialogTitle onClose={onCloseCreateCustomer} >Crear usuario</DialogTitle>

                <DialogContent>
                    <CustomerForm handleClose={onCloseCreateCustomer} onCloseModal={onCloseModal} onUpdateCustomers={onUpdateCustomers} modal={true} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={onCloseCreateCustomer} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>)
}

export default Customers